.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-boton {
        width: 2.8rem;
        margin-top: 3rem;
        margin-bottom: 2rem;
        filter: invert(100%);

        &:hover {
            cursor: pointer;
        }
        &:active {
            color: darken($blanco, 0.7);
            transform: scale(.6, .6) rotateZ(50deg);
            transition: all 0.1s ease-in-out;
        }

        @include tablet {
            margin-top: 0;
        }
    }
    
}
.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; 

    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
        }

        &:hover {
            color: $verde;
        }
    }
}

/** Menu de hamburguesa **/
.mobile-menu {
    @include tablet {
        display: none;
    }
&:active {
        transform: scale(.7, .7);
        transition: all 0.1s ease-in-out;
        }
    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}